.item {
  display: flex;
  margin-top: 10px;
}

.sold {
  margin-left: 15px;
  background-color: darkgray;
  color: white;
  font-size: 12px;
  font-weight: bolder;
  font-family: sans-serif;
  padding: 2px 4px;
  border-radius: 4px;
}

@media only screen and (min-width: 820px) {
  .item {
    float: left;
    width: 360px;
  }

  .item:nth-child(2n) {
    margin-left: 60px;
  }
}

.item-desc {
  display: flex;
  flex-direction: column;
  padding-left: 15px;
  width: 100%;
}

.item-name {
  font-family: 'Roboto Slab', serif;
  font-weight: bold;
  font-size: 20px;
}

.rarity-label {
  display: flex;
  align-items: center;
  padding-bottom: 4px;
  width: 100%;
}

.rarity-diamond {
  padding-right: 5px;
}

.rarity-score {
  font-size: 14px;
  font-family: sans-serif;
  color: darkgrey;
  padding-bottom: 3px;
}

.rarity-0 {
  color: #808080;
  white-space:nowrap;
  font-weight: 900;
  text-transform: uppercase;
}
.rarity-1 {
  color: #1e891e;
  white-space:nowrap;
  font-weight: 900;
  text-transform: uppercase;
}
.rarity-2 {
  color: #4068e0;
  white-space:nowrap;
  font-weight: 900;
  text-transform: uppercase;
}
.rarity-3 {
  color: #ff1493;
  white-space:nowrap;
  font-weight: 900;
  text-transform: uppercase;
}
.rarity-4 {
  color: #ffbe1b;
  white-space:nowrap;
  font-weight: 900;
  text-transform: uppercase;
}


