.App {
  display: flex;
  align-items: center;
  flex-direction: column;
  min-height: 100%;
}

html, body, #root, #root>div {
  min-height: 100vh;
}

.content {
  display: flex;
  flex-direction: column;
  width: 800px;
  font-family: 'Roboto Mono', monospace;
  text-align: left;
  align-items: flex-start;
  color: black;
  min-height: 100vh;
  max-width: 100%;
  padding: 0 10px;
  box-sizing: border-box;
}

.center {
  width: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
}

.opensea {
  background-color: #2196F3;
  color: white;
  text-transform: uppercase;
  text-decoration: none;
  padding: 8px 20px;
  font-size: 15px;
  font-weight: bold;
  border-radius: 5px;
  margin-top: 10px;
}

.opensea:hover {
  background-color: #1e89de;
}

.opensea:active {
  background-color: #1c80ce;
}

.section {
  color: coral;
  font-size: 30px;
  font-family: 'Roboto Slab', serif;
  margin: 50px 0 0 0;
}

.logo {
  flex-grow: 1;
  text-decoration: none;
  color: black;
}

.logo-text {
  font-size: 30px;
  font-family: 'Roboto Slab', serif;
  font-weight: 900;
  margin-left: 10px;
}

.header {
  display: flex;
  align-items: center;
  padding: 10px 0;
  width: 100%;
  position: sticky;
  top: 0;
  background-color: white;
}

.social {
  float: right;
  margin-left: 15px;
}

.legendary {
  color: #ffbe1b;
  white-space:nowrap;
  font-weight: 900;
}
.ultra-rare {
  color: #ff1493;
  white-space:nowrap;
  font-weight: 900;
}
.rare {
  color: #4068e0;
  white-space:nowrap;
  font-weight: 900;
}
.uncommon {
  color: #1e891e;
  white-space:nowrap;
  font-weight: 900;
}
.common {
  color: #808080;
  white-space:nowrap;
  font-weight: 900;
}
.light {
  color: lightgray;
}

.search {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  width: 100%;
}

.diamonds {
  display: flex;
  flex-direction: row;
  width: 90%;
  max-width: 500px;
  padding: 10px 0;
}

.flex-fill {
  flex-grow: 1;
}

.diamond {
  flex-grow: 0;
  width: 50px;
  max-width: 14%;
}

.footer {
  display: flex;
  font-family: sans-serif;
  font-size: 12px;
  bottom: 0;
  background-color: white;
  position: sticky;
  padding: 5px 0;
  width: 100%;
  align-items: center;
  color: darkgrey;
}

.footer div {
  display: grid;
}

.footer a {
  text-decoration: none;
  color: darkgrey;
}

.footer span {
  flex-grow: 1;
  text-align: right;
}